<script>
import { BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  data() {
    return {
      selected: 'All',
      email: '',
      modal: false,
      states: 'searchData',
      options: [
        { value: 'All', text: 'All' },
        { value: 'Buy', text: 'Buy' },
        { value: 'Sell', text: 'Sell' },
      ],
      errorfind: false,
    };
  },
  watch: {
    async selected(newValue, oldValue) {
      console.log('Selected option changed from', oldValue, 'to', newValue);
      if (newValue == 'Buy') {
        await this.changeType('buy');
        await this.getList();
      }
      if (newValue == 'Sell') {
        await this.changeType('sell');
        await this.getList();
      }
    },
  },
  computed: {
    ...mapGetters('tradeStore', ['tradeData', 'pageData', 'typeData']),
    filterStates() {
      return this.states.filter((item) => {
        return item.title.toLowerCase().includes(this.state.toLowerCase());
      });
    },
  },
  methods: {
    ...mapActions('tradeStore', ['getList', 'getPrevData', 'getNextData', 'changeType']),
    async prevPage() {
      console.log('prev');
      await this.getPrevData();
    },
    async nextPage() {
      await this.getNextData();
    },
  },

  components: {
    BRow,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
};
</script>
